.banner {
    position: relative;
    width: 100%;
    height: 120vh;
    background: url(/Nav-Scroll/static/media/back4.b12396ae.jpg);
    /* background: url('./back3.jpg') no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover; */
    background-size: cover;
    /* background-attachment: fixed; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-clip-path: circle(250vh at 35% -150vh);
            clip-path: circle(250vh at 35% -150vh);
  }
  
  
  .wrapper {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    /* border: 5px solid black; */
  }
  .wrapper .static-txt {
    color: black;
    font-size: 60px;
    font-weight: 500;
  }
  .wrapper .dynamic-txts {
    margin-left: 15px;
    height: 90px;
    /* line-height: 90px; */
    overflow: hidden;
  }
  .dynamic-txts li {
    list-style: none;
    color: white;
    font-size: 60px;
    font-weight: 500;
    position: relative;
    top: 0;
    -webkit-animation: slide 12s steps(4) infinite;
            animation: slide 12s steps(4) infinite;
    text-align-last: start;
  }
  @-webkit-keyframes slide {
    100% {
      top: -360px;
    }
  }
  @keyframes slide {
    100% {
      top: -360px;
    }
  }
  .dynamic-txts li span {
    position: relative;
    margin: 5px 0;
    line-height: 90px;
  }
  .dynamic-txts li span::after {
    content: "";
    position: absolute;
    left: 0;
    height: 100%;
    width: 100%;
    background: #dd0e04;
    border-left: 2px solid white;
    -webkit-animation: typing 3s steps(15) infinite;
            animation: typing 3s steps(15) infinite;
  }
  @-webkit-keyframes typing {
    40%,
    60% {
      left: calc(100% + 30px);
    }
    100% {
      left: 0;
    }
  }
  @keyframes typing {
    40%,
    60% {
      left: calc(100% + 30px);
    }
    100% {
      left: 0;
    }
  }
  
