* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.header {
    position: fixed;
    /* position: absolute; */
    top: 0;
    left: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    transition: 0.6s;
    padding: 40px 100px;
    z-index: 1;
  }
  
  .header.sticky {
    padding: 10px 100px !important;
    background: white;
  }
  
  .header .logo {
    position: relative;
    font-weight: 700;
    color: #fff;
    text-decoration: none;
    font-size: 2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: 0.6s;
    /* background: url("./favicon.png"); */
    /* background-size: cover; */
    width: 80px;
    height: 80px;
  }
  
  .image {
    width: 80px;
    background-blend-mode: soft-light !important;
  }
  .header ul {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .header ul li {
    position: relative;
    list-style: none;
  }
  
  .header ul li a {
    position: relative;
    margin: 0 8px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 500px;
    transition: 0.6s;
  }

  .header.sticky .logo {
    color: black;
  }
  
  .header.sticky ul li a {
    color: black;
  }
  
.navheader {
  /* color: #e8f1f2; */
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  background-color: #0d0a0b;
  overflow-x: scroll;
  /* border: 5px solid black; */
  position: fixed;
  width: 100%;
  z-index: 10;
}

.navheader::-webkit-scrollbar {
  display: none;
}

.navheader {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.navheader__icons {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.navheader__icon {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  cursor: pointer;
  color: #f3eff5;
  text-decoration: none;
}

.navheader > img {
  object-fit: contain;
  height: 80px;
}

.navheader__icon > .MuiSvgIcon-root {
  margin-left: auto;
  margin-right: auto;
  font-size: 25px;
}

.navheader__icon:hover > p {
  display: -webkit-inline-flex;
  display: inline-flex;
}

.navheader__icon > p {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: small;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-inline-flex;
  display: inline-flex;
  margin-bottom: 0px;
}

.navheader__icon:hover > .MuiSvgIcon-root {
  font-size: 35px;
}

.navheader__icon:hover > p {
  font-size: smaller;
}

